import CommonHeader from '@/components/layouts/CommonHeader.vue'
export default {
	components: {
		CommonHeader
	},
	data() {
		return {
			buylist: [], //订单列表
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			freight: 12.00, //运费
			note: '', //备注
			int: 1200, //抵扣积分
			deduction: 0, //抵扣价格
			info: {},
			recinfo: {
				id: 1,
				name: "大黑哥",
				head: "大",
				tel: "18816881688",
				address: {
					region: {
						"label": "广东省-深圳市-福田区",
						"value": [18, 2, 1],
						"cityCode": "440304"
					},
					detailed: '深南大道1111号无名摩登大厦6楼A2'
				},
				isDefault: true
			}

		};
	},
	onLoad(e) {
		this.getInfo(e.id)
	},
	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		getInfo(order_id) {
			uni.request({
				url: getApp().globalData.api + 'order/info',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: order_id
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.info = res.data.data
					this.loading = false
				},
				fail: (res) => {
					this.loading = false
				}
			});
		},
		toPay() {
			uni.showLoading({
				title: '正在提交订单...'
			})
			uni.request({
				url: getApp().globalData.api + 'order/submit',
				method: 'POST',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: this.info.id
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					uni.hideLoading();
					if(res.data.code == 200){
						// 去支付
						
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						})
					}
				},
				fail: (res) => {
					uni.hideLoading();
				}
			});
			// setTimeout(() => {
			// 	uni.setStorage({
			// 		key: 'paymentOrder',
			// 		data: paymentOrder,
			// 		success: () => {
			// 			uni.hideLoading();
			// 			uni.redirectTo({
			// 				url: "/pages/order/pay?amount=" + this.sumPrice
			// 			})
			// 		}
			// 	})
			// }, 1000)

		},
		//选择收货地址
		selectAddress() {
			uni.navigateTo({
				url: '../user/address/address?type=select'
			})
		}
	}
}
